import React, {props} from 'react';
import s from './Post.module.css';

const Post = (props) => {
    return (

        <div className={s.item}>
            <img src='https://i.pinimg.com/236x/a4/40/04/a4400453bad6d581ec203ad1455d0c8f--pretty-pics-pretty-pictures.jpg' />
            <div className={s.postText}>{props.message}</div>
        <div>
            <span>Like {props.likesCount}</span>
        </div>
        </div>

    )

}

export default Post;