import React, {props} from 'react';
import s from './Header.module.css';

const Header = () => {
    return (
        <header className={s.header}>
            <div className={s.mainLogo}>
                <img src='http://pngimg.com/uploads/vkontakte/vkontakte_PNG25.png' />
            </div>

        </header>
    )
}

export default Header;