import React, { props } from 'react';
import s from './Dialogs.module.css';
import { NavLink } from 'react-router-dom';
import Message from './Message/Message';
import DialogItem from './DialogItem/DialogItem';

const Dialogs = (props) => {

    let dialogs = [
        { id: 1, name: 'Arsenii' },
        { id: 2, name: 'Kolya' },
        { id: 3, name: 'Vitya' },
        { id: 1, name: 'Vitya' },
        { id: 2, name: 'Kolya' },
        { id: 3, name: 'Vitya' }
    ];

    let messages = [
        { id: 1, message: 'darova' },
        { id: 2, message: 'priver' },
        { id: 3, message: 'hello' },
        { id: 1, message: 'go dota' },
        { id: 2, message: 'go' },
        { id: 3, message: 'pognali' }
    ];

    let dialogsElements = dialogs.map(d => <DialogItem userName={d.name} id={d.id} />);

    let messagesElements = messages.map(m => <Message text={m.message} />);

    return (
        <div className={s.dialogs}>
            <div className={s.dialogs_items}>
                {dialogsElements}
            </div>
            <div className={s.messages}>
                {messagesElements}
            </div>
        </div>
    )
}

export default Dialogs;